import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import CircularProgress from '@mui/material/CircularProgress';
import { blue, red } from '@mui/material/colors';
// core import
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import React, { useEffect, useState } from 'react';

import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';

// Dialogs

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

/* ဘာတွေ လိုမလဲ?​ */

import {
  // selectors
  loginStatus,
  loginError,
  selectUserRegistrationValidationError,
  selectAccessToken,

  // actions

  // thunks
  // } from './../../../features/user/userSlice';
} from 'features/user/userSlice';

import {
  selectMenuError,
  selectMenuErrors,
  selectMenuStoreStatus,
  // selectors
  selectSelectedCategory,
  setMenuError,
  setMenuStoreStatus,
  setMenuErrors,
  selectSelectedCategoryList,
  //} from 'features/category/categorySlice';
} from 'features/main/mainSlice';

import {
  /*
  // selectors
  // main data (၂) မျိုး
  selectMenuList,
  selectSelectedMenu,
  // CRUD Status (၄) မျိုး
  selectMenuStatus,
  selectMenuStoreStatus,
  // selectMenuUpdateStatus,
  selectMenuDeleteStatus,
  // Error (၂) မျိုး
  selectMenuError,
  selectMenuErrors,

  // actions
  // local state ပြောင်းတာတွေ
  setCategories,
  setSelectedMenu,
  updateSelectedMenu,
  // local status ပြောင်းတာတွေ
  setMenuStatus,
  setMenuStoreStatus,
  setMenuUpdateStatus,
  setMenuDeleteStatus,
  // Validation Errors တွေ set လုပ်ဖို့
  setMenuErrors,
  */

  // thunks
  // CRUD Thunks  တွေ။
  selectMenuListAsyncThunk,
  storeMenuAsyncThunk,
  updateMenuAsyncThunk,
  deleteMenuAsyncThunk,
} from 'features/menu/menuSlice';
import { FormHelperText } from '@mui/material';

export default function MenuCreateView() {
  // core
  const navigate = useNavigate();
  const dispatch = useDispatch();

  // internal state
  const [formData, setFormData] = useState({
    placement: 0,
    shop_id: 0,
    name: '',
    description: '',
  });

  const [files, setFiles] = useState({
    background_image: null,
    shop_image: null,
  });

  // selector hooks
  // menu
  // လိုတာတွေပဲ Select လုပ်မလား?
  // အကုန် ယူမလား?
  // module တစ်ခုမှာ လိုတာတွေပဲယူလိုက်တာ ကောင်းပါတယ်။
  const selectedCategoryList = useSelector(selectSelectedCategoryList);
  const menuStoreStatus = useSelector(selectMenuStoreStatus);
  const error = useSelector(selectMenuError);
  const errors = useSelector(selectMenuErrors);
  // ဒါက supplimentry လိုတဲ့ data တွေ။
  // menu insert လုပ်ဖို့အတွက်ဆိုရင် selected category id လိုတယ်။
  const selectedCategory = useSelector(selectSelectedCategory);
  // user
  // ဒီမှာလည်း
  const accessToken = useSelector(selectAccessToken);

  const handleChange = (e) => {
    const { name, value } = e.target;
    console.log({ name, value });
    setFormData({ ...formData, [name]: value });
  };

  const handleFileChanges = (event) => {
    const { name, current_files } = event;
    console.log('Name & Files:', name, current_files);
    setFiles({ ...files, [name]: current_files.length == 0 ? null : current_files[0] });
  };

  const handleSubmit = (event) => {
    console.log(event);
    event.preventDefault();

    const data = new FormData(event.target);

    // validate form data
    // Validation logic
    const validationErrors = {};
    if (!formData.name) {
      validationErrors.name = 'Name is required';
    }
    if (!formData.category_id && !data.has('category_id')) {
      validationErrors.category_id = 'Need to choose one category';
    }

    // if (!formData.description) {
    //   validationErrors.description = 'Description is required';
    // }
    console.log(validationErrors);

    if (Object.keys(validationErrors).length > 0) {
      // alert("validation error");
      // setErrors(validationErrors);
      dispatch(setMenuErrors(validationErrors));
    } else {
      dispatch(setMenuErrors({}));
      
      // check local file and add to form data
      if (files['background_image'] !== undefined) {
        data.append('background_image', files['background_image']);
      }

      dispatch(storeMenuAsyncThunk(data));
    }
  };

  const handleClose = () => {
    dispatch(setMenuStoreStatus('idle'));
  };

  useEffect(() => {
    if (menuStoreStatus == 'succeeded') {
      // need to dispatch to set status as idle
      dispatch(setMenuStoreStatus('idle'));
      // should go back to get to shop detail
      // navigate("/shops");
      navigate(-1); // This is equivalent to pressing the back button
    }
  }, [menuStoreStatus]); // Include history as a dependency

  /*
    တကယ်လိုတဲ့ Field က Name, Description, Image
    Optional က placement
  */
  return (
    <>
      <Container component="main" maxWidth="xs">
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Typography component="h1" variant="h5">
            New Category
          </Typography>
          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <input
              name="access_token"
              value={accessToken}
              type="hidden"
              placeholder="Hidden Input"
            />
            {/* <input
              name="category_id"
              value={selectedCategory.id}
              type="hidden"
              placeholder="Hidden Input for Selected Shop Id"
            /> */}
            <input type="hidden" value={0} name="placement" placeholder="Menu Placement" />
            <input type="hidden" value={1} name="is_active" placeholder="Active Flag" />
            <input type="hidden" value={0} name="visit_count" placeholder="Visit Count" />
            <input type="hidden" value={'menu description'} name="description" placeholder="Visit Count" />

            <FormControl fullWidth>
              <InputLabel id="category-select">Menu</InputLabel>
              <Select
                autoFocus
                labelId="category-select"
                id="category-select"
                value={formData.category_id === undefined ? selectedCategoryList[0].id : formData.category_id}
                label="Menu"
                fullWidth
                name="category_id"
                onChange={handleChange}
                error={errors.hasOwnProperty('category_id')}
                helperText={errors.category_id}
              >
                {selectedCategoryList.map((category) => (
                  <MenuItem value={category.id}>{category.name}</MenuItem>
                ))}
                {/* <MenuItem value={10}>Ten</MenuItem>
                <MenuItem value={20}>Twenty</MenuItem>
                <MenuItem value={30}>Thirty</MenuItem> */}
              </Select>
              <FormHelperText sx={{color:'red'}}>{errors.category_id}</FormHelperText>
            </FormControl>

            {/* name */}
            <TextField
              id="name"
              label="Name"
              name="name"
              onChange={handleChange}
              error={errors.hasOwnProperty('name')}
              helperText={errors.name}
              autoComplete="name"
              margin="normal"
              required
              fullWidth
              autoFocus
            />

            {/* description */}
            {/* <TextField
              id="description"
              label="Description"
              name="description"
              autoComplete="description"
              onChange={handleChange}
              error={errors.hasOwnProperty('description')}
              helperText={errors.description}
              margin="normal"
              required
              fullWidth
            /> */}

            {/* <Typography>Menu Image {files['background_image']?.name}</Typography>
            <TextField
              type="file"
              name="background_image"
              onChange={handleChange}
              error={errors.hasOwnProperty('background_image')}
              helperText={errors.background_image}
              fullWidth
            /> */}

            <Box sx={{ m: 1, position: 'relative' }}>
              <Button
                type="submit"
                fullWidth
                variant="contained"
                sx={{ mt: 3, mb: 2 }}
                disabled={menuStoreStatus === 'loading'}
              >
                Create New Category
              </Button>
              {menuStoreStatus === 'loading' && (
                <CircularProgress
                  size={24}
                  sx={{
                    color: blue[500],
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    marginTop: '-12px',
                    marginLeft: '-12px',
                  }}
                />
              )}
            </Box>
          </Box>
        </Box>

        <Dialog
          open={menuStoreStatus === 'error'}
          onClose={handleClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">{'Error'}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">{error}</DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} autoFocus>
              OK
            </Button>
          </DialogActions>
        </Dialog>
      </Container>
    </>
  );
}
